
/* Slideshow container */
.slideshow-container {
    max-width:550px;
    width:100vw;
    position: relative;
    margin: 20px auto;
    img {
      width:200px;
      height:auto;
    }
  }

  /* Hide the images by default */
  .mySlides {
    display: none;
  }

  /* Next & previous buttons */
  .prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
  }

  /* Position the "next button" to the right */
  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }

  /* On hover, add a black background color with a little bit see-through */
  .prev:hover, .next:hover {
    background-color: rgba(0,0,0,0.8);
  }

  /* Caption text */
  .text {
    color: $color-font-brown;
    font-family: $font-headings;
    font-size: 27px;
    padding: 0 12px;
    position: absolute;
    top:0;
    right:0;
    text-align: center;
  }

  .price {
    color: $color-font-brown;
    font-family: $font-text;
    font-size: 20px;
    padding: 15px 12px;
    position: absolute;
    top:40px;
    right:0;
    text-align: center;
  }

  .shop {
    color:$color-font-brown;
    font-family: 'gotham black';
    font-size: 24px;
    text-transform: uppercase;
    border: 2px solid $color-font-brown;
    padding: 10px 15px;
    background-color: transparent;
    position: absolute;
    top:150px;
    right:50px;
    &:hover {
      border: 2px solid  #977a73;
        color: #977a73;
    }
  }

  /* The dots/bullets/indicators */
  .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
  }

  .active, .dot:hover {
    background-color: #717171;
  }

  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }

  @-webkit-keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }

  @keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }
